.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

div.minecraft {
  background-image: url("https://bloximages.chicago2.vip.townnews.com/buffalonews.com/content/tncms/assets/v3/editorial/2/0a/20a79e57-0bc0-5386-bedf-e8fad2777274/5ef11467b41df.image.jpg?resize=1200%2C744");
  background-repeat: no-repeat;
  height: 800px;
  width: auto;
  margin: 120px;
  margin-top: 0px;
  margin-bottom: 0px;
}

div.minecraft p {
  position: fixed;
  bottom: 10%;
  right: 25%;
}